  margin: 0;
  padding: 0;
  position: absolute;
  top: -10px;
  left: 0;
}

.switch {
  float: left;
  height: 14px;
  width: 14px;
  z-index: 1;
  background: radial-gradient(#faff6c 0%, #6d68fb 50%, #5e86ff 51%, #03006c 100%);
  border-radius: 8px;
  transition: left .2s;
  display: block;
  position: relative;
  top: -4px;
  left: -1px;
  box-shadow: 0 1px 1px #65727b, 0 0 1px #b6bdc2;
}

.toggle-bg input:checked ~ .switch {
